import './assets/styles/global.css'; 

const getBrowserLanguage = () => {
  const lang = navigator.language || navigator.userLanguage;
  return lang.includes('zh') ? 'zh' : 'en'; // ç®€å•ç¤ºä¾‹ï¼Œå¯ä»¥æ‰©å±•æ›´å¤šè¯­è¨€
};

// åˆ›å»º i18n å®ä¾‹
const i18n = createI18n({
  locale: getBrowserLanguage(), // é»˜è®¤è¯­è¨€ä¸ºç”¨æˆ·æµè§ˆå™¨çš„è¯­è¨€
  fallbackLocale: 'en', // å¦‚æœæ‰¾ä¸åˆ°å¯¹åº”çš„ç¿»è¯‘ï¼Œä½¿ç”¨çš„é»˜è®¤è¯­è¨€
  messages: {
    en,
    zh,
  },
});

const app = createApp(App);
app.use(ElementPlus);
app.use(router);
app.use(i18n);
app.mount('#app');
